import React from "react"
import { useTheme } from "../components/ThemeProvider";
import {Github, LinkedIn, Moon, Sun, Twitter} from "../components/icons";

const IndexPage = () => {
  const {themeMode, handleThemeChange} = useTheme();
  return (
    <div className=" dark:bg-lime-950 h-screen">
      <main className="px-10">
        <section>
          <nav className="py-10 mb-12 flex justify-end">
              <button className="cursor-pointer text-2xl" onClick={handleThemeChange}>{themeMode === "light" ? <Moon className="w-6"/> : <Sun className="w-6"/>}</button> 
          </nav>
          <div className="text-center p-10">
            <div className="flex row items-end justify-center">
              <h2 className="text-2xl py2 text-green-900 font-bold dark:text-white">Pat Ilano</h2>
              <h3 className="text-1xl py2 text-green-900 font-medium ml-3 dark:text-white opacity-90">@ilanopat</h3>
            </div>
            <h3 className="text-1xl py-2 opacity-80 dark:text-white">Devloping is my passion :3</h3>
          </div>
          <ul className="text-5xl flex justify-center gap-16 py-3 text-gray-600">
            <li>
              <a href="https://twitter.com/ilanopat">
                <Twitter className=" fill-green-900 w-6 dark:fill-white hover:opacity-90" />
              </a>
            </li>
            <li>
              <a href="https://github.com/ilanopat">
                <Github className=" fill-green-900 w-6 dark:fill-white hover:opacity-90"/>
              </a>
            </li>
            <li>
              <a href="https://linkedin.com/in/ilanopat">
                <LinkedIn className=" fill-green-900 w-6 dark:fill-white hover:opacity-90"/>
              </a>
            </li>
          </ul>
        </section>
      </main>
    </div>
)};

export default IndexPage;
